<script setup lang="ts">
import { XMarkIcon } from '@heroicons/vue/20/solid'
import { type Component, type HTMLAttributes } from 'vue'
import type { ClassValue } from 'clsx'

import { type AsTag, cn } from '@/shared/lib/style'

import type { Size, Variant } from './types'
import { badge, badgeDot, badgeRemove } from './lib'

export interface Props extends /* @vue-ignore */ HTMLAttributes {
  class?: ClassValue
  as?: Component | AsTag
  variant?: Variant
  size?: Size
  dot?: boolean
  pill?: boolean
  flat?: boolean
  removable?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  as: 'span',
  variant: 'primary',
  size: 'lg',
})

export type Emits = {
  remove: []
}

defineEmits<Emits>()
</script>

<template>
  <component
    :is="as"
    :class="cn(
      badge({ variant, size, dot, pill, flat, removable }),
      props.class ?? '',
    )"
  >
    <svg v-if="dot" :class="badgeDot({ variant })" viewBox="0 0 6 6">
      <circle cx="3" cy="3" r="3" />
    </svg>

    <slot />

    <button
      v-if="removable"
      aria-label="remove"
      :class="badgeRemove({ variant })"
      @click="$emit('remove')"
    >
      <XMarkIcon />
    </button>
  </component>
</template>
